import React, { useState, useEffect } from "react";
import "./BlogCard.css";
import { Link } from "@reach/router";

const BlogCard = ({
  title,
  summary,
  date,
  tags,
  likes: initialLikes,
  etitle,
}) => {
  const [hasLiked, setHasLiked] = useState(false);
  const [likes, setLikes] = useState(initialLikes);
  useEffect(() => {
    // 
    const liked = localStorage.getItem(`liked-${etitle}`);
    if (liked) {
      setHasLiked(true);
    }
  }, [etitle]);

  const handleLike = async () => {
    if (!hasLiked) {
      setLikes(likes + 1); // 
      setHasLiked(true);
      localStorage.setItem(`liked-${etitle}`, "true"); // 
      try {
        const response = await fetch(`/blog/blog.json/${title}/likes`, {
          method: "POST",
          headers: {
            "Content-Type": "application/json",
          },
          body: JSON.stringify({
            likes: likes + 1,
          }),
        });
      } catch (error) {
        alert(": " + error.message);
      }
    } else {
      alert("");
    }
  };
  return (
    <Link to={`/blog/blogs/${etitle}`}>
      <div className="blog-card">
        <Link to={`/blog/blogs/${etitle}`} className="blog-title">
          {title}
        </Link>
        <p className="blog-summary">{summary}</p>
        <div className="blog-meta">
          <span className="blog-date">{date}</span>
          <span className="blog-tags">{tags}</span>
          <span className="blog-likes" onClick={handleLike}>
             {likes}
          </span>
        </div>
      </div>
    </Link>
  );
};

export default BlogCard;
